<template>
  <div>
    <div style="padding-bottom: 20px;">
      <el-button type="primary" @click="addItem"></el-button>
    </div>

    <el-table :data="tData" style="width: 100%" border >
      <el-table-column prop="sum" label="">
        <template slot-scope="scope">
          <el-input v-model="scope.row.sum" placeholder="" type="number" @keyup.enter.native="comwpi(scope)" @blur="comwpi(scope)" @change="comwpi(scope)" ></el-input>
        </template>
      </el-table-column>

      <el-table-column prop="needle" label="">
        <template slot-scope="scope">
          <el-input v-model="scope.row.needle" placeholder="" type="number" @keyup.enter.native="comwpi(scope)" @blur="comwpi(scope)" @change="comwpi(scope)" ></el-input>
        </template>
      </el-table-column>

      <el-table-column prop="width" label="">
        <template slot-scope="scope">
          <el-input v-model="scope.row.width" placeholder="" type="number"  @keyup.enter.native="comwpi(scope)" @blur="comwpi(scope)" @change="comwpi(scope)"></el-input>
        </template>
      </el-table-column>

      <el-table-column prop="yarnlong" label="MM">
        <template slot-scope="scope">
          <el-input v-model="scope.row.yarnlong" placeholder="" type="number" @keyup.enter.native="comwpi(scope)" @blur="comwpi(scope)" @change="comwpi(scope)"></el-input>
        </template>
      </el-table-column>

      <el-table-column prop="yarn" label="S">
        <template slot-scope="scope">
          <el-input v-model="scope.row.yarn" placeholder="" type="number" @keyup.enter.native="comwpi(scope)" @blur="comwpi(scope)" @change="comwpi(scope)" ></el-input>
        </template>
      </el-table-column>

      <el-table-column prop="cpi" label="CPI">
        <template slot-scope="scope">
          <el-input v-model="scope.row.cpi" placeholder="" type="number" @keyup.enter.native="comwpi(scope)" @blur="comwpi(scope)" @change="comwpi(scope)"></el-input>
        </template>
      </el-table-column>

      <el-table-column prop="pull" label="%">
        <template slot-scope="scope">
          <el-input v-model="scope.row.pull" placeholder="" type="number" @keyup.enter.native="comwpi(scope)" @blur="comwpi(scope)" @change="comwpi(scope)"></el-input>
        </template>
      </el-table-column>

            <el-table-column prop="wpi" label="WPI">
        <template slot-scope="scope">
          <el-input v-model="scope.row.wpi" placeholder="" type="number" @keyup.enter.native="comwpi(scope)" @blur="comwpi(scope)" @change="comwpi(scope)"></el-input>
        </template>
      </el-table-column>

            <el-table-column prop="weight" label="">
        <template slot-scope="scope">
          <el-input v-model="scope.row.weight" placeholder="" type="number" @keyup.enter.native="comwpi(scope)" @blur="comwpi(scope)" @change="comwpi(scope)"></el-input>
        </template>
      </el-table-column>

            <el-table-column prop="eng" label="ENG">
        <template slot-scope="scope">
          <el-input v-model="scope.row.eng" placeholder="" type="number" @keyup.enter.native="comwpi(scope)" @blur="comwpi(scope)" @change="comwpi(scope)"></el-input>
        </template>
      </el-table-column>
      <el-table-column width="80">
        <template slot-scope="scope">
          <el-button type="danger" @click="delItem(scope)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>

export default {
  data() {
    return {
      tData: [],
      a:0.00082,
      b:0.018367,
      c:0.98,
    }
  },
  methods: {
    addItem() {
      const objData = {
        sum: '',
        needle: '',
        width: '',
        yarnlong: '',
        yarn: '',
        cpi: '',
        pull: '',
        wpi: '',
        weight: '',
        eng: '',
      }
      this.tData.push(Object.assign({}, objData))
    },
    delItem(scope) {
      this.tData.splice([scope.$index], 1)
    },
    comwpi (scope) {
        this.tData[scope.$index].wpi = ((this.tData[scope.$index].sum -  this.tData[scope.$index].needle) / this.tData[scope.$index].width ).toFixed(2)
        this.tData[scope.$index].weight = (this.tData[scope.$index].cpi * this.tData[scope.$index].wpi * this.tData[scope.$index].yarnlong * this.b / this.tData[scope.$index].yarn).toFixed(2)
        this.tData[scope.$index].eng =(this.tData[scope.$index].weight * this.tData[scope.$index].width * this.a * this.c  ).toFixed(3)
        
    },

    // renderHeader(h, { column, $index }) {
    // return h
    // } 
  }
}
</script>

<style scoped>
.footer-field {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  font-size: 14px;
  color: #464646;
}
</style>