<script>

export default {
  data() {
    return {
      showDelAction: false,
      swpi: 0,
      anserSum: 0,
      proSum: 0,
      weisum: 0,
      wsum: 0,
      nwsum: 0,
      ylsum: 100,
      tData: [],
      pData: []
    }
  },
  methods: {
    addItem() {
      const objData = {
        yarntype: '',
        proportion: 0,
        wpi: 0,
        artyarnlong: 0,
        enyarn: 0,
        dan: 0,
        cpi: 0,
        yarnlong: 0,
        weight: 0,
        wash: 0,
        nowash: 0,
        pullratio: 0,
      }
      this.tData.push(Object.assign({}, objData))
    },
    delItem(scope) {
      this.tData.splice([scope.$index], 1)
    },
    delItemAction() {
      this.showDelAction = !this.showDelAction
    },
    yarntypeSelect(scope) {
      if (scope.row.yarntype === 'ctn') {
        this.tData[scope.$index].wpi = this.cwpi
      } else if (scope.row.yarntype === 'poly') {
        this.tData[scope.$index].wpi = this.pwpi
      }
      this.cpiChange(scope)
    },

    prochange(scope) {
      let tmpArr = []
      this.tData.forEach(row => {
        tmpArr.push(parseFloat(row.proportion))
      })
      this.proSum = eval(tmpArr.join('+'))
      this.cpiChange(scope)
    },

    yarnlongChange(scope) {
      this.tData[scope.$index].yarnlong = this.tData[scope.$index].artyarnlong * 50 / this.pwpi

      this.cpiChange(scope)
    },

    enyarnChange(scope) {
      if (scope.row.yarntype === 'poly') {
        this.tData[scope.$index].enyarn = (5319 / this.tData[scope.$index].dan).toFixed(2)
      }
      this.cpiChange(scope)
    },

    danChange(scope) {
      this.enyarnChange(scope)
    },

    cpiChange(scope) {
      this.tData[scope.$index].cpi = this.tData[scope.$index].proportion * this.ccpi
      this.wpisum = Number(this.cwpi) + Number(this.pwpi)
      this.ylsum = Number(this.pullratio) + Number(this.ylsum) + '%'

      this.washChange(scope)
      this.nowashChange(scope)
      this.weightChange(scope)
    },

    weightChange(scope) {
      this.tData[scope.$index].weight = (this.tData[scope.$index].cpi * this.cwpi * this.tData[scope.$index].yarnlong / this.tData[scope.$index].enyarn * 0.018367).toFixed(2)
      let tmpArr = []
      this.tData.forEach(row => {
        tmpArr.push(parseFloat(row.weight))
      })
      this.weisum = eval(tmpArr.join('+'))
    },
    washChange(scope) {
      this.tData[scope.$index].wash = (this.tData[scope.$index].weight * 0.95).toFixed(2)
      let tmpArr = []
      this.tData.forEach(row => {
        tmpArr.push(parseFloat(row.wash))
      })
      this.wsum = eval(tmpArr.join('+')).toFixed(2)
    },
    nowashChange(scope) {
      this.tData[scope.$index].nowash = (this.tData[scope.$index].weight * 0.97).toFixed(2)
      let tmpArr = []
      this.tData.forEach(row => {
        tmpArr.push(parseFloat(row.nowash))
      })
      this.nwsum = eval(tmpArr.join('+')).toFixed(2)
    },
    solidchange() {
      this.swpi = (this.ssum - this.sneedle) / this.swidth
    }
  }
}
</script>

