<template>
  <div>
    <div style="padding-bottom: 20px;">
      <el-button type="primary" @click="addItem" ></el-button>
      <el-button type="danger" @click="delItemAction" v-if="tData.length > 0"></el-button>
    </div>

    <el-table :data="tData" style="width: 100%" border>
      <el-table-column prop="yarntype" label="">
        <template slot-scope="scope">
          <el-select v-model="scope.row.yarntype" placeholder="" @keyup.enter.native="yarntypeSelect(scope)" @change="yarntypeSelect(scope)">
            <el-option label="POLY" value="poly"></el-option>
            <el-option label="CTN" value="ctn"></el-option>
            <el-option label="" value="mix"></el-option>
          </el-select>
        </template>
      </el-table-column>

      <el-table-column prop="modcount" label="(F)">
        <template slot-scope="scope">
          <el-input v-model="scope.row.modcount" placeholder="" type="number" @keyup.enter.native="modcountChange(scope)" @blur="modcountChange(scope)" @change="modcountChange(scope)"></el-input>
        </template>
      </el-table-column>

      <el-table-column prop="enyarn" label="(S)">
        <template slot-scope="scope">
          <el-input v-model="scope.row.enyarn" placeholder="" type="number" @keyup.enter.native="enyarnChange(scope)" @blur="enyarnChange(scope)" @change="enyarnChange(scope)"></el-input>
        </template>
      </el-table-column>

      <el-table-column prop="yarnlong" label="(MM)">
        <template slot-scope="scope">
          <el-input v-model="scope.row.yarnlong" placeholder="" type="number" @keyup.enter.native="yarnlongChange(scope)" @blur="yarnlongChange(scope)" @change="yarnlongChange(scope)"></el-input>
        </template>
      </el-table-column>

      <el-table-column prop="dan" label="(D)">
        <template slot-scope="scope">
          <el-input v-if="scope.row.yarntype === 'poly'" v-model="scope.row.dan" placeholder="" type="number" @keyup.enter.native="danChange(scope)" @blur="danChange(scope)" @change="danChange(scope)"></el-input>
        </template>
      </el-table-column>

      <el-table-column prop="ctnpercent" label="CTN">
        <template slot-scope="scope">
          <el-input v-if="scope.row.yarntype === 'mix'" v-model="scope.row.ctnpercent" placeholder="" type="number" @keyup.enter.native="ctnpercentChange(scope)" @blur="ctnpercentChange(scope)" @change="ctnpercentChange(scope)"></el-input>
        </template>
      </el-table-column>

      <el-table-column prop="anser" label="Anser1">
        <template slot-scope="scope">
          <el-input v-model="scope.row.anser" placeholder="" type="number" @keyup.enter.native="anserChange(scope)" @blur="anserChange(scope)" @change="anserChange(scope)"></el-input>
        </template>
      </el-table-column>

      <el-table-column prop="yarncount" label="%">
        <template slot-scope="scope">
          <el-input v-model="scope.row.yarncount" placeholder="" type="number" @keyup.enter.native="yarncountChange(scope)" @blur="yarncountChange(scope)" @change="yarncountChange(scope)"></el-input>
        </template>
      </el-table-column>
      <!-- <el-table-column prop="percentage" label="%">
            <template slot-scope="scope">
              <el-input v-model="scope.row.percentage" placeholder="" type="number"></el-input>
            </template>
          </el-table-column> -->
      <el-table-column width="80" v-if="showDelAction">
        <template slot-scope="scope">
          <el-button type="danger" @click="delItem(scope)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <div class="footer-field">
      <span>TOTAL</span>
      <span>: {{modcountSum}}</span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span>POLY : {{polyConst}}</span>
      <span></span>
      <span>Anser: {{anserSum}}</span>
      <span></span>
    </div>

    <el-table :data="pData" style="width: 20%" border show-summary>
      <el-table-column prop="yarntype" label=""></el-table-column>
      <el-table-column prop="percentage" label="%"></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      polyConst: 5319,
      anserSum: 0,
      showDelAction: false,
      modcountSum: 0,
      tData: [],
      pData: [
        { yarntype: 'POLY', percentage: 0 },
        { yarntype: 'CTN', percentage: 0 },
        { yarntype: '', percentage: 0 },
      ]
    }
  },
  methods: {
    addItem() {
      const objData = {
        yarntype: '',
        modcount: '',
        enyarn: '',
        yarnlong: '',
        dan: '',
        ctnpercent: 0,
        anser: '',
        yarncount: '',
        percentage: '',
      }
      this.tData.push(Object.assign({}, objData))
    },
    delItem(scope) {
      this.tData.splice([scope.$index], 1)
      this.anserChange()
    },
    delItemAction() {
      this.showDelAction = !this.showDelAction
    },
    yarntypeSelect(scope) {
      if (scope.row.yarntype === 'poly') {
        this.tData[scope.$index].enyarn = this.tData[scope.$index].dan / this.polyConst
      } else if (scope.row.yarntype === 'ctn') {
        this.tData[scope.$index].enyarn = 30
      } else if (scope.row.yarntype === 'mix') {
        this.tData[scope.$index].enyarn = 10
      }
      this.countAnser(scope)
      this.sumPrecentage()
    },
    danChange(scope) {
      if (scope.row.yarntype === 'poly') {
        this.tData[scope.$index].enyarn = (this.tData[scope.$index].dan / this.polyConst).toFixed(9)
        this.countAnser(scope)
      }
    },
    modcountChange(scope) {
      let tmpArr = []
      this.tData.forEach(row => {
        tmpArr.push(parseFloat(row.modcount))
      })
      this.modcountSum = eval(tmpArr.join('+'))
      this.countAnser(scope)
    },
    enyarnChange(scope) {
      this.countAnser(scope)
    },
    yarnlongChange(scope) {
      this.countAnser(scope)
    },
    countAnser(scope) {
      if (scope.row.yarntype === 'poly') {
        this.tData[scope.$index].anser = (this.tData[scope.$index].modcount * this.tData[scope.$index].enyarn * this.tData[scope.$index].yarnlong).toFixed(9)
        this.anserChange(scope)
      } else {
        this.tData[scope.$index].anser = (this.tData[scope.$index].modcount * this.tData[scope.$index].yarnlong / this.tData[scope.$index].enyarn).toFixed(9)
        this.anserChange(scope)
      }
    },
    yarncountChange() {

    },
    anserChange() {
      let tmpArr = []
      this.tData.forEach(row => {
        tmpArr.push(parseFloat(row.anser))
      })
      this.anserSum = eval(tmpArr.join('+'))
      this.sumPrecentage()
      this.countYarnCount()
    },
    countYarnCount() {
      this.tData.forEach(item => {
        item.yarncount = (item.anser / this.anserSum * 100).toFixed(2)
      })
    },
    ctnpercentChange() {
      this.sumPrecentage()
    },
    sumPrecentage() {
      /*  */
      let polyTmpArr = []
      let mixTmpArr = []

      let preCtnTmpArr = []
      let ctnTmpArr = []
      this.tData.forEach(row => {
        if (row.yarntype === 'poly') {
          polyTmpArr.push(parseFloat(row.yarncount))
        } else if (row.yarntype === 'mix') {
          let tmpV = parseFloat(row.yarncount) * (1 - parseFloat(row.ctnpercent))
          mixTmpArr.push(tmpV)

          let ctmpV = parseFloat(row.ctnpercent) * parseFloat(row.yarncount)
          preCtnTmpArr.push(ctmpV)
        } else if (row.yarntype === 'ctn') {
          ctnTmpArr.push(parseFloat(row.yarncount))
        }
      })
      this.pData.forEach(row => {
        if (row.yarntype === 'POLY') {
          row.percentage = eval(polyTmpArr.join('+'))
        } else if (row.yarntype === '') {
          row.percentage = eval(mixTmpArr.join('+'))
        } else if (row.yarntype === 'CTN') {
          let preSum = 0
          if (preCtnTmpArr.length > 0) {
            preSum = eval(preCtnTmpArr.join('+'))
          }
          let tmpctnSum = eval(ctnTmpArr.join('+'))
          row.percentage = tmpctnSum + preSum

        }
      })
    }
    // renderHeader(h, { column, $index }) {
    // return h
    // }
  }
}
</script>

<style scoped>
.footer-field {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  font-size: 14px;
  color: #464646;
}
</style>